import React ,{ Component }from "react";
import { FiCheck , FiLayers , FiUsers , FiInstagram, FiActivity, FiAperture } from "react-icons/fi";

const ServiceList = [
    {
        icon: <FiCheck />,
        title: 'Crypto & Cars',
        description: 'Featuring West Coast Customs, CoinCar & Leading Blockchain Experts centered around car culture from classics to modern exotics - Join us on Sept. 28th in Downtown Tampa Florida',
        image: 'https://64.media.tumblr.com/a6c872794f2d71dc67fbfe28bdbe7dd6/6b310df09faa9e9d-b9/s1280x1920/26b25d4c60a6f804ffb089807b0231e3b2bcade4.jpg',
        url: '/events/crypto-cars-092821'
    }
]


class ServiceThree extends Component{
    render(){
        const {column } = this.props;
        const ServiceContent = ServiceList.slice(0 , this.props.item);
        
        return(
            <React.Fragment>
                <div className="row">
                    {ServiceContent.map( (val , i) => (
                        <div className={`${column}`} key={i}>
                            <a href={`${val.url}`}>
                                <div className="service service__style--2" style={{ backgroundImage:`url(${val.image})` }}>
                                    <div className="content">
                                        <h3 className="title">{val.title}</h3>
                                        <p>{val.description}</p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    ))}
                </div>
            </React.Fragment>
        )
    }
}
export default ServiceThree;
